.cell{
    border: 1px solid black;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
}
.row{
    display: flex;
}
.gameOver{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
    color: white;
}
.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column
  }
  .round{
    margin-bottom: 20px;
    font-size: 20px;
  }
  .finish{
    margin-top: 20px;
    font-size: 20px;
  }